/**
 * @file
 * Layout paragraphs builder styles.
 */

.lp-builder {
  min-height: 30px;
  position: relative;
}

.js-lpb-region {
  box-sizing: border-box;
  cursor: default;
  min-height: 40px;
  position: relative;
}

.lpb-empty-container__wrapper {
  position: relative;
}

.lpb-empty-container__wrapper .lpb-section-menu__wrapper {
    bottom: 20px;
  }

.lpb-empty-container {
  padding-bottom: 30px;
  text-align: center;
}

.lpb-empty-toggle {
  min-height: 20px;
}

.lpb-empty-message {
  padding-bottom: 40px;
  text-align: center;
}

.is-dragging .js-lpb-region,
.is-navigating .js-lpb-region {
  outline-color: hsla(240, 100%, 50%, 0.5);
  outline-style: dotted;
  outline-width: 1px;
}

.is-dragging .t-reversed .js-lpb-region {
  outline-color: hsl(0, 0%, 100%);
  outline-style: dotted;
  outline-width: 1px;
}

.js-lpb-component {
  cursor: grab;
  outline-color: transparent;
  outline-style: solid;
  outline-width: 1px;
  position: relative;
  transition: all 0.15s linear;
}

.js-lpb-component.is-navigating {
    outline-color: hsla(240, 100%, 50%, 0.5);
    outline-style: solid;
    outline-width: 3px;
  }

.lp-builder:not(.is-navigating) .js-lpb-component:hover,
.lp-builder:not(.is-navigating) .js-lpb-component[focus-within] {
  outline-color: hsla(240, 100%, 50%, 0.5);
  outline-style: solid;
  outline-width: 1px;
}

.lp-builder:not(.is-navigating) .js-lpb-component:hover,
.lp-builder:not(.is-navigating) .js-lpb-component[focus-within] {
  outline-color: hsla(240, 100%, 50%, 0.5);
  outline-style: solid;
  outline-width: 1px;
}

.lp-builder:not(.is-navigating) .js-lpb-component:hover,
.lp-builder:not(.is-navigating) .js-lpb-component:focus-within {
  outline-color: hsla(240, 100%, 50%, 0.5);
  outline-style: solid;
  outline-width: 1px;
}

.js-lpb-component:hover .js-lpb-region,
.js-lpb-component[focus-within] .js-lpb-region {
  outline: 1px dotted hsla(240, 100%, 50%, 0.5);
}

.js-lpb-component:hover .js-lpb-region,
.js-lpb-component[focus-within] .js-lpb-region {
  outline: 1px dotted hsla(240, 100%, 50%, 0.5);
}

.js-lpb-component:hover .js-lpb-region,
.js-lpb-component:focus-within .js-lpb-region {
  outline: 1px dotted hsla(240, 100%, 50%, 0.5);
}

.lp-builder:not(.is-navigating) .js-lpb-component:hover .js-lpb-region:hover,
.lp-builder:not(.is-navigating) .js-lpb-component[focus-within] .js-lpb-region[focus-within] {
  outline: 1px solid hsla(240, 100%, 50%, 0.5);
}

.lp-builder:not(.is-navigating) .js-lpb-component:hover .js-lpb-region:hover,
.lp-builder:not(.is-navigating) .js-lpb-component[focus-within] .js-lpb-region[focus-within] {
  outline: 1px solid hsla(240, 100%, 50%, 0.5);
}

.lp-builder:not(.is-navigating) .js-lpb-component:hover .js-lpb-region:hover,
.lp-builder:not(.is-navigating) .js-lpb-component:focus-within .js-lpb-region:focus-within {
  outline: 1px solid hsla(240, 100%, 50%, 0.5);
}

.lpb-layout {
  padding: 20px;
}

.lpb-layout .lpb-layout-label {
    border: 1px dotted hsl(0, 0%, 80%);
    font-size: small;
    padding: 5px 10px;
    position: absolute;
    right: 0;
  }

.js-lpb-btn--add {
  position: absolute;
}

.lpb-controls__wrapper {
  position: absolute;
}

.lpb-controls {
  background-color: hsl(0, 0%, 100%);
  border-radius: 0 0 6px;
  box-shadow: 0 1px 1px hsla(0, 0%, 0%, 0.25), 0 1px 3px hsla(0, 0%, 0%, 0.25);
  cursor: default;
  display: flex;
  left: 0;
  opacity: 0;
  padding: 5px;
  position: absolute;
  top: 0;
  z-index: 80;
}

.lpb-controls:hover,
  .lpb-controls[focus-within] {
    z-index: 90;
  }

.lpb-controls:hover,
  .lpb-controls:focus-within {
    z-index: 90;
  }

.lpb-controls.is-layout {
    background-color: hsl(201, 100%, 30%);
    border-radius: 6px 6px 0 0;
    box-shadow: none;
    color: hsl(0, 0%, 100%);
    left: auto;
    right: -1px;
    top: -40px;
  }

.lpb-controls a {
    font-size: 0;
  }

.lp-builder:not(.is-navigating) .js-lpb-component:hover > .lpb-controls,
.lp-builder:not(.is-navigating) .js-lpb-component[focus-within] > .lpb-controls {
  opacity: 1;
}

.lp-builder:not(.is-navigating) .js-lpb-component:hover > .lpb-controls,
.lp-builder:not(.is-navigating) .js-lpb-component:focus-within > .lpb-controls {
  opacity: 1;
}

.lpb-controls-label {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 30px;
  padding: 0 5px 0 10px;
}

.lpb-drag,
.lpb-up,
.lpb-down,
.lpb-edit,
.lpb-duplicate,
.lpb-delete {
  border-radius: 3px;
  cursor: pointer;
  height: 30px;
  margin: 0;
  opacity: 0.5;
  padding: 0;
  width: 30px;
}

.is-layout .lpb-drag,
.is-layout .lpb-up,
.is-layout .lpb-down,
.is-layout .lpb-edit,
.is-layout .lpb-duplicate,
.is-layout .lpb-delete {
  opacity: 1;
}

