.lpb-tooltiptext {
  background-color: hsl(0, 0%, 0%);
  bottom: 100%;
  color: hsl(0, 0%, 100%);
  left: -7px;
  margin-bottom: -2px;
  opacity: 0;
  padding: 10px;
  pointer-events: none;
  position: absolute;
  text-align: center;
  transition: all 0.15s linear;
  white-space: nowrap;
  width: auto;
  z-index: 90;
}

.lpb-tooltiptext::after {
    border-color: hsl(0, 0%, 0%) transparent transparent;
    border-style: solid;
    border-width: 7px;
    content: ' ';
    left: 20px;
    position: absolute;
    top: 100%;
  }

.lp-builder:not(.is-dragging) .lpb-tooltip--hover:hover + .lpb-tooltiptext {
  transition-delay: 1s;
}

.lp-builder:not(.is-dragging) .lpb-tooltip--focus:focus + .lpb-tooltiptext,
.lp-builder:not(.is-dragging) .lpb-tooltip--hover:hover + .lpb-tooltiptext,
.lpb-tooltiptext--visible {
  opacity: 1;
}

