/**
 * @file
 * Styles for action links.
*/

.action-links {
  list-style: none;
  margin-block: 0;
  margin-inline: 0;
  padding-block: 0;
  padding-inline: 0;
}

.action-links li {
    display: inline-block;
  }

.action-links li a {
      color: hsl(var(--blue-9));
    }

.action-links-item {
  display: inline-block;
}
