/**
 * @file
 * Main form styles.
 */

.fieldset__label.form-required::after,
.required-mark::after {
  background-image: url('../images/required.svg');
  background-repeat: no-repeat;
  background-size: 0.5rem 0.5rem;
  content: '';
  display: inline-block;
  height: 1rem;
  margin-inline: 0.3em;
  vertical-align: text-top;
  width: 1rem;
}

@media screen and (-ms-high-contrast: active) {

.fieldset__label.form-required::after,
.required-mark::after {
    background-image: url("data:image/svg+xml,%3Csvg height='16' width='16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='m0 7.562 1.114-3.438c2.565.906 4.43 1.688 5.59 2.35-.306-2.921-.467-4.93-.484-6.027h3.511c-.05 1.597-.234 3.6-.558 6.003 1.664-.838 3.566-1.613 5.714-2.325l1.113 3.437c-2.05.678-4.06 1.131-6.028 1.356.984.856 2.372 2.381 4.166 4.575l-2.906 2.059c-.935-1.274-2.041-3.009-3.316-5.206-1.194 2.275-2.244 4.013-3.147 5.206l-2.856-2.059c1.872-2.307 3.211-3.832 4.017-4.575-2.081-.402-4.058-.856-5.93-1.356' fill='%23ffffff'/%3E%3C/svg%3E%0A")
}
  }

.field-multiple-table + .form-item__description {
  -webkit-margin-before: 0;
          margin-block-start: 0;
}

.form-actions {
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
  margin-block: var(--size-1);
}

.form-actions .button,
  .form-actions .action-link {
    margin-block: var(--size-1);
  }

.form-actions .ajax-progress--throbber {
    align-self: center;
  }

.form--inline > * {
    -webkit-margin-before: var(--size-105);
            margin-block-start: var(--size-105);
    -webkit-margin-after: 0;
            margin-block-end: 0;
    -webkit-margin-start: 0;
            margin-inline-start: 0;
    -webkit-margin-end: var(--size-1);
            margin-inline-end: var(--size-1);
    max-width: 100%;
    vertical-align: top; /* Ensure proper alignment if description is present. */
  }

.form--inline .form-actions {
    -webkit-margin-before: var(--size-3);
            margin-block-start: var(--size-3);
  }

.form--inline .form-wrapper {
    -webkit-margin-before: 35px;
            margin-block-start: 35px;
  }

.form--inline .button {
    margin-block: 0;
    -webkit-margin-end: 0;
            margin-inline-end: 0;
  }
